@use "../assets/styles/mixins" as mixin;

.item {
  height: 84px;
  border-radius: 0px 50px 50px 50px;
  background-color: var(--primary);
  color: var(--white-100);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;

  @include mixin.xs {
    padding: 0 30px;
  }
}
