@use "../../assets/styles/mixins" as mixin;

main {
  background-color: var(--white-12);

  .major {
    height: 100%;
    .global {
      width: 100%;
      gap: 100px;
      margin-top: 170px;
      align-items: flex-start;
      justify-content: space-between;
      background-color: var(--white-12);
      color: var(--white-100);
      @include mixin.lg-lt {
        margin-top: 100px;
        flex-direction: column;
        align-items: center;
        gap: 40px;
      }

      .textZone {
        max-width: 552px;
        padding-top: 80px;

        @include mixin.sm-lt {
          padding-top: 0;
        }
        @include mixin.xs {
          padding-left: 20px;
          padding-right: 20px;
        }

        .preTitle,
        .descriptif {
          font-size: 22px;
          @include mixin.sm-lt {
            font-size: 20px;
          }
        }
        .preTitle,
        .descriptif,
        label {
          font-family: gt planar;
        }
        .descriptif {
          margin-top: 50px;
          line-height: 157%;
        }
        .title {
          font-size: 64px;
          padding-top: 10px;
          @include mixin.sm-lt {
            font-size: 34px;
          }
        }
      }

      // bloc à droite
      .formGlobal {
        z-index: 0;
        min-width: 624px;
        min-height: 810px;
        // max-height: 920px;
        padding: 80px;
        flex-wrap: nowrap;
        background-color: var(--white-18);
        border-radius: 10px;
        width: 50%;

        @include mixin.sm-lt {
          min-width: 100%;
          padding: 20px;
          border-radius: 0;
        }

        .info {
          margin-top: 20px;
          @include mixin.sm-lt {
            display: none;
          }
        }

        .info2 {
          line-height: 168%;
          margin-top: 50px;
          margin-bottom: 50px;
          font-size: 20px;
          @include mixin.sm-lt {
            display: none;
          }
        }
        .info,
        .info2 {
          font-family: GT planar;
        }

        .formTitle {
          max-width: 336px;
          font-size: 36px;
          @include mixin.sm-lt {
            padding-top: 50px;
          }
        }

        form {
          padding-top: 0px;
          label {
            padding-top: 50px;
            font-size: 16px;
            color: var(--white-50);
          }
          input {
            width: 100%;
            padding-top: 20px;
            border: none;
            border-bottom: 1px solid var(--white-50);
            background-color: var(--white-18);
            font-size: 22px;
            font-family: GT planar;
            color: var(--white-100);

            @include mixin.sm-lt {
              font-size: 20px;
            }
          }

          input:focus {
            outline: none;
          }

          .formSection {
            padding-top: 50px;
            @include mixin.sm-lt {
              font-size: 5px;
            }
          }

          button {
            border-style: none;
            margin-top: 45px;
            width: 157px;
            height: 58px;
            border-radius: 50px;
            color: white;
            background-color: var(--primary);
            font-size: 20px;
            font-family: gt planar;

            .vector {
              padding-left: 15px;
            }
          }
        }
      }
    }
    .blackSpace {
      background-color: var(--white-0);
      width: 100%;
      height: 200px;
      margin-top: -200px;
    }
  }
}
